body {
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  display: -webkit-box;
  display: -moz-box;
  display: -ms-flexbox;
  display: -webkit-flex;
}

.showcase {
  display: flex;
  justify-content: space-between;
}

.showcase .updated-json {
  margin-left: 5em;
  border-style: inset;
  padding: 1em;
  max-height: 30em;
  overflow-y: scroll;
}

.showcase .updated-json .disclaimer {
  font-style: italic;
  color: #4d5258;
}
